import "./App.css";
import NavigationBar from "./components/NavigationBar";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { HashRouter } from "react-router-dom";
import GuessGamePage from "./pages/GuessGamePage";
import SliderPage from "./pages/Slider";
import HomePage from "./pages/HomePage";
import Footer from "./components/Footer";
import Game1WikiPage from "./pages/Game1WikiPage";
import Game2WikiPage from "./pages/Game2WikiPage";
import Game3WikiPage from "./pages/Game3WikiPage";
import SignupOrSignin from "./pages/SignupOrSignin";
import GamesListFromDatabase from "./pages/GamesListFromDatabase";
import "bootstrap/dist/css/bootstrap.min.css";
function App() {
  return (
    <div className="App">
      <HashRouter>
        <NavigationBar></NavigationBar>
        <Switch>
          <Route
            path="/FireBase-CRUD-React/SignUporSignIn"
            component={SignupOrSignin}
          ></Route>
          <Route
            path="/FireBase-CRUD-React/GamesListFromDatabase"
            component={GamesListFromDatabase}
          ></Route>
          <Route
            path="/FireBase-CRUD-React/GuessGamePage"
            component={GuessGame}
          ></Route>
          <Route
            path="/FireBase-CRUD-React/SliderPage"
            component={Slider}
          ></Route>
          <Route
            path="/FireBase-CRUD-React/Game1WikiPage"
            component={Game1Wiki}
          ></Route>
          <Route
            path="/FireBase-CRUD-React/Game2WikiPage"
            component={Game2Wiki}
          ></Route>
          <Route
            path="/FireBase-CRUD-React/Game3WikiPage"
            component={Game3Wiki}
          ></Route>
          <Route path="/FireBase-CRUD-React" component={Home}></Route>
        </Switch>
        <Footer></Footer>
      </HashRouter>
    </div>
  );
}

const Home = () => {
  return (
    <div>
      <HomePage></HomePage>
    </div>
  );
};

const GuessGame = () => {
  return (
    <div>
      <GuessGamePage></GuessGamePage>
    </div>
  );
};

const Slider = () => {
  return (
    <div>
      <SliderPage></SliderPage>
    </div>
  );
};

const Game1Wiki = () => {
  return (
    <div>
      <Game1WikiPage></Game1WikiPage>
    </div>
  );
};

const Game2Wiki = () => {
  return <Game2WikiPage></Game2WikiPage>;
};

const Game3Wiki = () => {
  return <Game3WikiPage></Game3WikiPage>;
};

export default App;
